

 .userDiv{
    display: flex;
    flex-direction: column;
    width: 30vw;
    background-color: white;
    justify-content: flex-start;
    align-items: flex-start;
    align-items: center;
    padding: 20px;
} 
#btnn{
    color: #0275d8;;
    background-color: transparent;
    display: inline;
    width: 10vw;
    height: 40px;
    border-radius: 4px;
    margin-right: 40px;
    margin-top: 15px;
    margin-left: 30px;
    font-size: 20px;
    background-color: transparent;
    border: none;
    cursor: pointer;
}
.logout{
    color: red;
    background-color: transparent;
    display: inline;
    width: 10vw;
    height: 40px;
    border-radius: 4px;
    margin-right: 40px;
    margin-top: 15px;
    margin-left: 30px;
    font-size: 20px;
    background-color: transparent;
    border: none;
    cursor: pointer;

}
#reset{
    color: green;
    background-color: transparent;
    border: none;
    cursor: pointer;

}
.error{
    color: red;
}