.main{
    text-align: center;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: none;


}
.heading{
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: none;
}
.second{
    height: 35vh;
    width: 30vw;
    display: flex;
    flex-direction: column;
    background-color:rgb(233, 224, 213);
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    padding: 10px;
    border: none;

}  
h1{
    color: white;
    border: none;

}
.email{
    width: 25vw;
    height: 40px;
    border-radius: 4px;
    margin: 10px;
    font-size: 20px;
    border: none;


} 
.text{
    width: 25vw;
    height: 40px;
    border-radius: 4px;
    margin: 10px;
    border: none;

} 
.pass{
    width: 25vw;
    height: 40px;
    border-radius: 4px;
    margin: 10px;
    font-size: 20px;
    border: none;



} 
.log{
    display: flex;
    flex-direction: row;
    width: 30vw;
    background-color: transparent;
}
.login{ 
    display: inline;
    width: 10vw;
    height: 40px;
    border-radius: 4px;
    margin-right: 40px;
    margin-top: 15px;
    margin-left: 30px;
    color: white;
    font-size: 20px;
    background-color: #5cb85c;

    border: none;


}
.signup{
    display: inline;
    width: 10vw;
    height: 40px;
    background-color: #0275d8;

    border-radius: 4px;
    margin-right: 40px;
    margin-top: 15px;
    margin-left: 30px;
    color: white;
    font-size: 20px;
    border: none;

}
.img{
    position: sticky;
    margin-left: -35px;
    margin-top: -6px;
    opacity: 0.3;
}
input {
    /* width: 250px; */
    padding: 5px;
    -webkit-box-sizing: border-box; 
    -moz-box-sizing: border-box;    
    box-sizing: border-box;  
    border: none;

}